
import { defineComponent, ref, watch, computed } from "vue";
// import Dropdown3 from "@/view/content/dropdown/Dropdown3.vue";

export default defineComponent({
  name: "WidgetDemografi",
  props: {
    items: {
      type: Array,
      required: true,
    },
  },
  components: {
    // Dropdown3
  },
  setup(props) {
    const options = ref({
      chart: {
        type: "donut",
        fontFamily: "inherit",
      },
      plotOptions: {
        pie: {
          customScale: 0.94,
          expandOnClick: false,
          donut: {
            size: "75%",
          },
        },
      },
      labels: [],
      // colors: ["#fec703", "#00a3ff", "#22d48a"],
      legend: {
        show: false,
      },
      dataLabels: {
        enabled: false,
      },
      animation: {
        animateScale: true,
        animateRotate: true,
      },
    });

    const dataSeries = ref<number[]>([]);

    const totalPercentage = computed(() =>
      (props.items as any)[0].series.reduce((prev, next) => prev + next)
    );

    watch(
      () => props.items,
      async (newVal) => {
        const newValues = newVal as any;
        const labels = newValues[0].name;
        const series = newValues[0].series;

        dataSeries.value = series;
        options.value = {
          ...options.value,
          ...{
            labels: labels,
          },
        };
      }
    );

    return {
      dataSeries,
      options,
      totalPercentage,
    };
  },
});
