
import { defineComponent, ref, watch } from "vue";

export default defineComponent({
  name: "WidgetChartSelling",
  props: {
    items: {
      type: Array,
      required: false,
      default: () => [
        {
          name: "Omset",
          data: [44, 55, 57, 56, 61, 58],
        },
        {
          name: "Profit",
          data: [76, 85, 101, 98, 87, 105],
        },
      ],
    },
  },
  components: {},
  setup(props) {
    const dataSeries = ref<any[]>([]);
    const chartOptions = ref({
      chart: {
        id: "realtime",
        type: "line",
        height: 350,
        animations: {
          enabled: true,
          easing: "linear",
          dynamicAnimation: {
            speed: 1000,
          },
        },
        toolbar: {
          show: false,
        },
        zoom: {
          enabled: false,
        },
      },
      // plotOptions: {
      //   bar: {
      //     horizontal: false,
      //     columnWidth: ["40%"],
      //     endingShape: "rounded",
      //   },
      // },
      dataLabels: {
        enabled: false,
      },
      legend: {
        show: false,
      },
      stroke: {
        show: true,
        width: 10,
        curve: "smooth",
      },
      xaxis: {
        categories: ["Feb", "Mar", "Apr", "May", "Jun", "Jul"],
        axisBorder: {
          show: false,
        },
        axisTicks: {
          show: false,
        },
        labels: {
          style: {
            colors: "#A1A5B7",
            fontSize: "12px",
          },
        },
      },
      yaxis: {
        labels: {
          style: {
            colors: "#A1A5B7",
            fontSize: "12px",
          },
        },
      },
      fill: {
        opacity: 1,
      },
      colors: ["#FFF1EA", "#E60023"],
      grid: {
        borderColor: "#EFF2F5",
        strokeDashArray: 4,
        yaxis: {
          lines: {
            show: true,
          },
        },
      },
    });

    watch(
      () => props.items,
      async (newVal) => {
        const newValues = newVal as any;
        const data = newValues[0].series;
        const categories = newValues[0].categories;

        dataSeries.value = data;
        chartOptions.value = {
          ...chartOptions.value,
          ...{
            xaxis: {
              categories: categories,
              axisBorder: {
                show: false,
              },
              axisTicks: {
                show: false,
              },
              labels: {
                style: {
                  colors: "#A1A5B7",
                  fontSize: "12px",
                },
              },
            },
          },
        };
      }
    );

    return {
      dataSeries,
      chartOptions,
    };
  },
});
